import { Injectable } from '@angular/core';
import { Storage } from '@ionic/storage';


@Injectable()
export class NoteService {

  private notes: {title: string}[] = [];

  constructor(public storage: Storage) {    
  }

    saveNote(note: {title: string}) {
      this.notes.push(note);
    }

    getAllNotes() {
      return [...this.notes];
      
    }
}



